@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;500;800&display=swap);
body{height:100vh;background:#eee3e3}.h1{font-family:"Roboto Slab",serif;font-weight:800;font-size:2.2rem;text-transform:uppercase}.h2{font-family:"Roboto Slab",serif;font-weight:500;font-size:2rem}.h3{font-family:"Roboto Slab",serif;font-weight:500;font-size:1.2rem}p{font-family:"Roboto Slab",serif;font-weight:300}.description_container p{font-size:1.8rem}.video_container p{font-size:1rem}.main_container{display:grid;grid-template-columns:5% 60% 35%;grid-template-rows:.5fr 2fr 3fr;grid-template-areas:". search search" ". tv side" ". desc side"}.search_container{grid-area:search}.tv_container{grid-area:tv}.frame_container{grid-area:tv}.video_container{grid-area:side}.description_container{grid-area:desc}.search_container{display:flex;justify-content:center;align-items:center;flex-direction:column;margin-top:3rem}.search_container__form{position:relative;width:30rem;background-color:#9edbe7;border:1px solid #07687b}.search_container__form input,.search_container__form button{height:4rem;border:0;font-family:"Roboto Slab",serif;font-weight:300;font-size:2rem}.search_container__form input[type=search]{outline:0;width:100%;padding:0 1rem;-webkit-appearance:none;-moz-appearance:none;appearance:none;-webkit-transition:all .4s cubic-bezier(0, 0, 0.43, 1.49);transition:all .4s cubic-bezier(0, 0, 0.43, 1.49);-webkit-transition-property:width,border-radius;transition-property:width,border-radius;z-index:1;position:relative}.search_container__form button{display:none;position:absolute;cursor:pointer;top:0;right:0;width:6rem;background-color:#9edbe7;-webkit-transition:all .5s cubic-bezier(0, 0, 0.43, 1.49);transition:all .5s cubic-bezier(0, 0, 0.43, 1.49);-webkit-transition-property:background-color,color;transition-property:background-color,color;font-size:2rem}.search_container__form button:hover{background-color:#07687b;color:#9edbe7}.search_container__form input:not(:placeholder-shown){width:calc(100% - 6rem)}.search_container__form input:not(:placeholder-shown)+button{display:block}.tv_container{position:relative;justify-self:center;width:500px;height:400px;margin-top:5rem;border:4px solid #000;background:#994d06;background:linear-gradient(140deg, #994d06 0%, black 100%);box-shadow:3px 3px 3px #000}.tv_container__kinescope{position:relative;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:400px;height:350px;border:2px solid #000;border-radius:50%/10%;background:#000;background:-webkit-gradient(linear, left top, left bottom, from(black), color-stop(51%, #dfdfdf), to(black));background:linear-gradient(180deg, black 0%, #dfdfdf 51%, black 100%);box-shadow:3px 3px 3px #000}.tv_container__kinescope::after{content:"";position:absolute;top:0;left:0;width:400px;height:350px;border-radius:50%/10%;background-size:5px 100px;background-repeat:repeat;background-image:-webkit-repeating-radial-gradient(31% 21%, closest-corner, rgba(255, 255, 255, 0.1), rgba(0, 255, 0, 0.1), rgba(3, 0, 255, 0.1) 20%),-webkit-repeating-radial-gradient(91% 51%, closest-corner, rgba(255, 255, 255, 0.1), rgba(255, 255, 1, 0.1), rgba(55, 255, 255, 0.1) 20%);-webkit-filter:grayscale(100%);filter:grayscale(100%);-webkit-animation:noise 50s ease infinite;animation:noise 50s ease infinite}@-webkit-keyframes noise{0%{background-position:0% 100%}100%{background-position:100% 0%}}@keyframes noise{0%{background-position:0% 100%}100%{background-position:100% 0%}}.video_item{display:flex;align-items:center;cursor:pointer;border-bottom:1px dashed #000;margin:1rem}.video_item__image{margin:.5rem;max-width:180px}.video_item:hover{background-color:#9edbe7}.video_container{padding:1rem;margin-top:5rem;width:80%;border-left:1px solid #000}.frame_container{position:relative;justify-self:center;margin-top:5rem}.frame_container__video{position:relative;top:25px;width:400px;height:350px;border:2px solid #000;border-radius:50%/10%}.description_container{width:80%;margin:2rem auto}
